body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.site-layout-content .banner {
  padding-bottom: '24px';
}
.site-layout-content img {
  margin-bottom: 24px;
  width: 100%;

}
.slick-next {
  right: -45px;
}
.slick-slider .slick-prev:before, .slick-slider .slick-next:before{
  font-family: none;
  font-size: 25px;
  line-height: 1;
  opacity: .75;
  color: white;
}
.ant-menu-dark.ant-menu-horizontal.ant-menu-dark.ant-menu-horizontal > .ant-menu-item:hover {
  background-color: rgb(0, 129, 164);
}
.ant-menu-dark.ant-menu-horizontal.ant-menu-dark.ant-menu-horizontal > .ant-menu-item:active {
  background-color: rgb(0, 129, 164);
}
.ant-menu-overflow .ant-menu .ant-menu-root .ant-menu-horizontal .ant-menu-dark .ant-menu.ant-menu-dark .ant-menu-item-selected, .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background-color: rgb(0, 129, 164);
}
.ant-menu-item-selected {
  background-color: rgb(0, 129, 164) !important;
}

